/* Global */
input {
  line-height: 0;
}

.check-box-area {
  padding: 5px 0 5px 0;
  .text {
    color: #272e4c;
    font-size: 11px;
  }
  .check-box {
    &.MuiCheckbox-root {
      padding: 0px;
      .MuiTouchRipple-root {
        display: none;
      }
    }
    &.MuiRadio-root {
      margin: -9px;
    }
    &.MuiIconButton-colorSecondary.Mui-checked {
      color: #3ab77d;
    }
    .MuiSvgIcon-root {
      height: 1.25rem;
    }
  }
}

.detailed-dropdown {
  padding: 5px 0 5px 0;
  width: 100%;
  .text {
    color: #272e4c;
    opacity: 0.7;
    font-size: 11px;
  }
  .autocomplete {
    display: flex;
    .detail {
      background: #ffffff;
      border: 1px solid #272e4c4d;
      border-top-left-radius: 4px;
      border-bottom-left-radius: 4px;
      font-size: 11px;
      padding: 8px;
      white-space: nowrap;
    }
    .detail-error {
      background: #ffffff;
      border: 1px solid #e53935;
      border-top-left-radius: 4px;
      border-bottom-left-radius: 4px;
      font-size: 11px;
      padding: 8px;
      white-space: nowrap;
    }
    .detail-disabled {
      background: #ffffff;
      border: 1px solid rgb(62, 70, 85);
      border-top-left-radius: 4px;
      border-bottom-left-radius: 4px;
      font-size: 11px;
      padding: 8px;
      white-space: nowrap;
      opacity: 0.5;
    }
    .actual-dropdown {
      width: 100%;
      font-size: 11px;
      .actual-dropdown__control {
        box-shadow: none;
        border: 1px solid #272e4c4d;
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
        border-left: none;
        min-height: 0;
        .actual-dropdown__value-container {
          padding: 4px 8px 4px 8px;
          .actual-dropdown__single-value {
            margin: 0;
          }
        }
        .actual-dropdown__indicators {
          .actual-dropdown__indicator-separator {
            display: none;
          }
          .actual-dropdown__dropdown-indicator {
            padding: 0;
          }
        }
      }
      .actual-dropdown-error__control {
        box-shadow: none;
        border: 1px solid #e53935;
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
        border-left: none;
        min-height: 0;
        .actual-dropdown-error__value-container {
          padding: 4px 8px 4px 8px;
          .actual-dropdown-error__single-value {
            margin: 0;
          }
        }
        .actual-dropdown-error__indicators {
          .actual-dropdown-error__indicator-separator {
            display: none;
          }
          .actual-dropdown-error__dropdown-indicator {
            padding: 0;
          }
        }
      }
      .actual-dropdown__control--is-disabled {
        border-color: rgb(62, 70, 85);
        background-color: #ffffff;
        opacity: 0.5;
      }
    }
  }
}

