    }
    .error {
      border: 1px solid #e53935;
    }
    .right {
      border-top-right-radius: 4px;
      border-bottom-right-radius: 4px;
      margin-left: -1px;
    }
    .left {
      border-top-left-radius: 4px;
      border-bottom-left-radius: 4px;
    }
    .text-field {
      border: 1px solid #272e4c4d;
      padding: 8px 8px;
      font-size: 11px;
