      }
      .actual-dropdown-error__control {
        box-shadow: none;
        border: 1px solid #e53935;
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
        border-left: none;
        min-height: 0;
        .actual-dropdown-error__value-container {
          padding: 4px 8px 4px 8px;
          .actual-dropdown-error__single-value {
            margin: 0;
          }
        }
        .actual-dropdown-error__indicators {
          .actual-dropdown-error__indicator-separator {
            display: none;
          }
          .actual-dropdown-error__dropdown-indicator {
            padding: 0;
          }
        }
      }
      .actual-dropdown__control--is-disabled {
        border-color: rgb(62, 70, 85);
        background-color: #ffffff;
        opacity: 0.5;
      }
    }
  }
}

.input-field-container {
  padding: 5px 0 5px 0;
  width: 100%;
  .text {
    color: #272e4c;
    opacity: 0.7;
    font-size: 11px;
  }
  .text-disabled {
    border-color: #9ea3ac;
    opacity: 0.5;
  }
  .text-field {
    width: 100%;
    padding: 8px 8px;
    font-size: 11px;
    background: #ffffff;
    border: 1px solid #272e4c4d;
    border-radius: 4px;
    .MuiInputBase-input {
      padding: 0;
      font-size: 11px;
      line-height: 0;
      height: inherit;
    }
    div[class*='chipContainer'] {
      min-height: 0;
      margin-bottom: 0;
      div[class*='-chip'] {
        font-size: 11px;
        margin: -5px 8px -5px 0;
      }
      .MuiChip-root {
        height: 1.5rem;
        .MuiSvgIcon-root {
          width: 1rem;
        }
      }
    }
    div[class*='underline']:before {
      display: none;
    }
    div[class*='underline']:after {
      display: none;
    }
  }
  .text-field:hover {
    border-color: #000000;
  }
  .text-field:focus {
    outline: none;
    border: #3ab77d solid 1px;
  }
  .text-field-disabled {
    border-color: #9ea3ac;
    opacity: 0.5;
  }
  .text-field-disabled:hover {
    border-color: #9ea3ac;
  }
}

.dropdown {
  padding: 5px 0 5px 0;
  width: 100%;
  .text {
    font-size: 11px;
    color: #272e4c;
    opacity: 0.7;
  }
  .text-disabled {
    font-size: 11px;
    color: #9ea3ac;
    opacity: 0.5;
  }
  /* This is the new styple implementation for dropdowns */
  .actual-dropdown {
    font-size: 11px;
    .actual-dropdown__control {
      box-shadow: none;
      border: 1px solid #272e4c4d;
      min-height: 0;
      .actual-dropdown__value-container {
        padding: 4px 8px 4px 8px;
        .actual-dropdown__single-value {
          margin: 0;
        }
      }
      .actual-dropdown__indicators {
        .actual-dropdown__indicator-separator {
          display: none;
        }
        .actual-dropdown__dropdown-indicator {
          padding: 0;
        }
      }
    }
    .actual-dropdown__control--is-focused {
      border: #3ab77d solid 1px !important;
    }
    .actual-dropdown-error__control {
      box-shadow: none;
      border: 1px solid #e53935;
      min-height: 0;
      .actual-dropdown-error__value-container {
        padding: 4px 8px 4px 8px;
        .actual-dropdown-error__single-value {
          margin: 0;
        }
      }
      .actual-dropdown-error__indicators {
        .actual-dropdown-error__indicator-separator {
          display: none;
        }
        .actual-dropdown-error__dropdown-indicator {
          padding: 0;
        }
      }
    }
    .actual-dropdown__control--is-disabled {
      border-color: rgb(62, 70, 85);
      background-color: #ffffff;
      opacity: 0.5;
    }
  }
  .actual-dropdown__control:hover {
    border-color: #000000;
  }
  .actual-dropdown__control:focus {
    border: #3ab77d solid 1px !important;
  }
}

.text-button {
  cursor: pointer;
}

.icon-button {
  color: #4b505c;
  opacity: 0.5;
  cursor: pointer;
  display: inline-block;
  margin: 0 0.2rem;
}

.chip {
  &.MuiChip-root {
    background-color: #ffffff00;
    font-style: italic;
    font-size: 11px;
    height: 1.25rem;

    .MuiChip-deleteIcon {
      width: 14px;
    }
  }
}

.date-picker {
  padding: 5px 0 5px 0;
  width: 100%;
  .text {
    color: #272e4c;
    opacity: 0.7;
    font-size: 11px;
  }
  .text-field {
    width: 100%;
    padding: 8px 8px;
    font-size: 11px;
    background: #ffffff;
    border: 1px solid #272e4c4d;
    border-radius: 4px;
    .MuiInput-underline:before {
      display: none;
    }
    .MuiInput-underline:after {
      display: none;
    }
    .MuiInputBase-input {
      padding: 0;
      font-size: 11px;
    }
    .MuiIconButton-root {
      padding: 0;
    }
  }
}

.custom-uploader {
  padding: 5px 0 5px 0;
  width: 100%;
  .text {
    color: #272e4c;
    opacity: 0.7;
    font-size: 11px;
  }
  .upload-box {
    border: 2px dashed #1e20714d;
    border-radius: 5px;
    color: #7b8099;
    background-color: #e6e8ee;
    padding: 24px;
    text-align: center;
    line-height: 1.25;
    .upload-file {
      font-size: 12px;
      color: #272e4c8f;
    }
  }
}

.complex-table {
  background-color: #dddfe3;
  box-shadow: 0px 3px 6px #00000029;
  position: relative;
  .header {
    padding: 0 20px 0 10px;
    margin: 0;

    // .link {
    //     text-align: center;
    //     line-height: 45px;
    // }
  }
  .item {
    background: #ffffff 0% 0% no-repeat padding-box;
    border-top: 1px solid #f5f5f5;
    // border-top: 1px solid #272E4C;
    // padding: 16px;
  }
}

.tabs {
  position: fixed;
  background-color: #f5f7fb;
  box-shadow: 4px 1px 6px #00000029;
  z-index: 1;
  left: calc((100% - 85px) * 0.25 + 85px);
  right: 0;

  .MuiTab-root {
    font-weight: 100;
    min-width: 0px;
    opacity: 0.5;
  }
  .MuiTab-wrapper {
    color: #151c1b;
  }
  .PrivateTabIndicator-root-169 {
    height: 4px;
  }
  .MuiTab-textColorInherit.Mui-selected {
    color: #151c1b;
    font-weight: 500;
  }
}

.table {
  margin: 5px 0 5px 0;
  box-shadow: 0px 3px 6px #00000029;
  .MuiTable-root {
    width: 100%;
    padding: 5px 0 5px 0;
  }
  .MuiTableCell-root {
    border-bottom: none;
  }
  .MuiTableCell-head {
    color: #000000;
    background-color: #dddfe3;
    padding: 10px 0 5px 10px;
    line-height: 1;
  }
  .MuiTableCell-body {
    font-size: inherit;
    padding: 1rem;
    line-height: 1;
  }
}
.pagination {
  &.MuiTableCell-root {
    width: 10000px;
  }
  &.MuiTablePagination-root {
    padding: 0;
  }
  .MuiTablePagination-toolbar {
    min-height: 0;
    height: 2rem;
  }
  .MuiTablePagination-spacer {
    flex: none;
  }
}

.custom-modal {
  display: flex;
  align-items: center;
  justify-content: center;
  .modal {
    background-color: #ffffff;
    padding: 1rem 1.5rem;
    outline: none;
    width: 25rem;
    border-radius: 5px;
  }
  .header {
    font-size: 1rem;
    font-weight: 600;
    margin: 5px 0px 10px 0px;
  }
  .content {
    font-size: 13px;
    font-weight: 400;
    margin: 5px 0px 10px 0px;
  }
  .bottom {
    margin: 5px 0px 10px 0px;
    text-align: center;
  }
}

.text-tabs {
  &.MuiTabs-root {
    min-height: 0;
  }
  .MuiTab-root {
    line-height: 1;
    font-weight: 400;
    text-align: left;
    min-height: min-content;
    padding: 0;
    text-align: left;
    min-width: fit-content;
  }
  .MuiTabs-indicator {
    display: none;
  }
  .Mui-selected {
    color: #1e2071;
  }
}
